<template>
  <div id="app">
    <Header />
    <Sidenav />

    <main id="main">
      <transition mode="out-in">
        <router-view />
      </transition>
    </main>

    <MenuFooter />
    <Footer />
  </div>
</template>

<script>
import Header from './components/Header.vue'
import Sidenav from '@/components/Sidenav.vue'
import MenuFooter from '@/components/MenuFooter.vue'
import Footer from '@/components/Footer.vue'

export default {
  name: 'App',
  components: {
    Header,
    Sidenav,
    MenuFooter,
    Footer
  }
}
</script>

<style>
/* GRID GERAL */

.estrutura {
  max-width: 1188px;
  margin: 0 auto;
  padding: 0 32px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-areas:
    "header"
    "sidenav"
    "main"
    "menu_footer"
    "footer";
}

@media (max-width: 960px) {
  .estrutura {
    margin: 0 16px 0 16px;
    padding: 0;
    grid-template-columns: 100%;
    grid-template-areas:
      "header"
      "sidenav"
      "main"
      "menu_footer"
      "footer";
  }
}

#app {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  max-width: 1188px;
  margin: 0 auto;
  padding: 0 32px;
}

#main {
  flex: 1;
}

@media (max-width: 960px) {
  #app {
    margin: 0 16px 0 16px;
    padding: 0;
  }
}

body,
h1,
h2,
p,
ul,
li,
a,
img,
input {
  margin: 0px;
  padding: 0px;
}

body {
  font-family: "Montserrat", Open Sans, Helvetica, Arial, sans-serif;
  color: #222222;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

a:hover {
  color: #333;
}

img {
  display: block;
  max-width: 100%;
}

* {
  box-sizing: border-box;
}

/* STYLES DO TEXTO */
.categoria,
.secao {
  color: #333;
  border-bottom: 1px solid #eab883;
  font-size: 0.8em;
  font-weight: 600;
  padding-bottom: 3px;
}

.titulo_pagina {
  text-align: center;
}

.titulo {
  font-family: "Cormorant Garamond", serif;
  color: #e08f8f;
  display: inline;
  text-align: center;
  margin: 0 auto;
  font-weight: 500;
  font-size: 2.5em;
  border-bottom: 1px solid #eab883;
}

.titulo_sub {
  font-family: "Cormorant Garamond", serif;
  color: #e08f8f;
  display: block;
  text-align: center;
  margin: 20px 0;
  font-weight: 600;
  font-size: 1.8em;
}

.descricao_pagina {
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  display: block;
  text-align: center;
  margin: 20px 0;
  font-weight: 600;
  font-size: 1.75em;
}

.titulo_item {
  grid-column: span 2;
  color: #4a5a69;
  font-family: "Cormorant Garamond", serif;
  display: inline;
  text-align: center;
  margin: 40px 0;
  font-weight: 700;
  font-size: 2.5em;
}

.subtitulo_item {
  color: #B4AE8A;
  font-size: 1.125em;
  font-weight: 600;
  display: block;
  margin-top: 5px;
}

.conteudo {
  font-size: 1.25em;
  line-height: 1.6;
  text-align: left;
}

.data_hora,
.data {
  color: #e08f8f;
  font-size: 0.8em;
}
/*********************/

/* FORMULRIOS */
input,
textarea {
  background: #fff;
  border: 1px solid #4a5a69;
  font-size: 1.25em;
  padding: 11px;
  height: 50px;
  width: 50%;
  display: block;
}

.bt_enviar {
  background: #e08f8f;
  padding: 16px auto;
  color: #fff;
  border: 0px;
  cursor: pointer;
  font-size: 0.875em;
  font-weight: 500;
  height: 50px;
  width: 20%;
}

.bt_enviar:hover {
  background: #ff7864;
}
/***************/

/* EFEITOS DE ANIMAO */
.link,
.link2,
.link4,
.link5,
.link7 {
  transition: all 0.2s;
}

.link:hover {
  transform: scale(1.05);
}

.link2:hover {
  transform: scale(1.02);
}

.link4:hover {
  transform: scale(1.04);
}

.link7:hover {
  transform: scale(1.07);
}

.link5:hover {
  transform: scale(1.1);
}
/* */

.v-enter,
.v-leave-to {
  opacity: 0;
}

.v-enter {
  transform: translate3d(0, -20px, 0);
}

.v-leave-to {
  transform: translate3d(0, 20px, 0);
}

.v-enter-active {
  /* .v-leave-active {*/
  transition: all 0.5s;
}

/* Slow-motion Zoom Container */
.img-hover-zoom--slowmo img {
  transform-origin: 50% 65%;
  transition: transform 2s, filter 1.5s ease-in-out;
  filter: brightness(110%);
}

/* The Transformation */
.img-hover-zoom--slowmo:hover img {
  filter: brightness(100%);
  transform: scale(1.1);
}

</style>



