<template>
  <div class="titulo_pagina">
    <h1 class="titulo">Estrutura</h1>
    <section>
      <transition mode="out-in">
        <div
          v-if="estruturas && estruturas.length"
          key="estuturas"
          class="box_3grid"
        >
          <div
            v-for="estrutura in estruturas"
            :key="estrutura.Titulo"
            class="card_3grid"
          >
            <a href="#">
              <img src="../img/estrutura1.jpg" />
            </a>
            <div class="card_3grid_div">
              <h2 class="card_3grid_link">{{ estrutura.Titulo }}</h2>
            </div>
          </div>
        </div>
        <PaginaCarregando key="carregando" v-else />
      </transition>
    </section>
  </div>
</template>

<script>
import { db } from '../firebase.js'

export default {
  name: 'estrutura',
  data () {
    return {
      estruturas: {}
    }
  },
  firestore: {
    estruturas: db.collection('Estruturas').orderBy('DataHoraCadastro', 'desc')
  },
  methods: {
    getNoticia (titulo) {
      db.collection('Estruturas')
        .doc(titulo)
        .get()
    }
  }
}
</script>

<style scoped>
/* CARD COM 3 FOTOS NA LINHA */

.box_3grid {
  grid-area: box_3grid;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: auto;
  justify-content: space-between;
  gap: 40px;
  margin: 40px 0;
}

.card_3grid {
  flex: 1;
}

.card_3grid_div {
  display: block;
  text-align: center;
  margin-top: 20px;
}

.card_3grid_link {
  display: block;
  margin-top: 10px;
  text-align: center;
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.75em;
  font-weight: 600;
  line-height: 1.1;
}

.card_3grid_link:hover {
  color: #000;
}

@media (max-width: 960px) {
  .box_3grid {
    grid-template-columns: 1fr 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
  .card_3grid img {
    margin: 0 auto;
  }
  .card_3grid_div_link {
    margin-top: 2px;
  }
}

@media (max-width: 760px) {
  .box_3grid {
    grid-template-columns: 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
}
/*****************************/
</style>



