<template>
  <section class="content">
    <SliderNoticias />
    <Destaque />
    <HomeGrid3 />
    <div class="box_titulo">
      <span>reas Pastorais</span>
    </div>
    <HomeAreasPastorais />
  </section>
</template>

<script>
import SliderNoticias from '../components/SliderNoticias.vue'
import Destaque from '../components/Destaque.vue'
import HomeGrid3 from '../components/HomeGrid3.vue'
import HomeAreasPastorais from '../components/HomeAreasPastorais.vue'
import AcaoConta from '../components/AcaoConta.vue'

export default {
  name: 'home',
  components: {
    SliderNoticias,
    Destaque,
    HomeGrid3,
    HomeAreasPastorais,
    AcaoConta
  }
}
</script>

<style scoped>
.content {
  max-width: 1124px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 388px;
  grid-template-areas:
    "slider_noticias slider_noticias destaque"
    "box_3grid box_3grid box_3grid"
    "box_titulo box_titulo box_titulo"
    "areas_pastorais areas_pastorais areas_pastorais"
    "acao_conta acao_conta acao_conta";
}

@media (max-width: 960px) {
  .content {
    margin: 0 16px 0 16px;
    grid-template-columns: 100%;
    grid-template-areas:
      "slider_noticias"
      "destaque"
      "box_3grid"
      "box_titulo"
      "areas_pastorais"
      "acao_conta";
  }
}

.box_titulo {
  grid-area: box_titulo;
  text-align: center;
  margin-bottom: 20px;
  position: relative;
}

.box_titulo span {
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 2.2em;
  border-bottom: 1px solid #eab883;
  font-weight: 700;
}
</style>



