<template>
  <section class="acao_conta">
    <form>
      <input id="nome" name="nome" type="nome" v-model="nome" placeholder="seu nome" />
      <input id="telefone" name="telefone" type="telefone" v-model="telefone" placeholder="seu telefone" />
      <input id="email" name="email" type="email" v-model="email" placeholder="seu e-mail" />
      <input id="mensagem" name="mensagem" type="mensagem" v-model="mensagem" placeholder="sua mensagem" />
      <input class="bt_enviar" type="submit" value="ENVIAR MENSAGEM" />
    </form>
  </section>
</template>

<script>
export default {
  name: 'AcaoConta'
}
</script>

<style scoped>
.acao_conta {
  grid-area: acao_conta;
  background: #fefae8;
  padding: 20px 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-items: center;
}

.acao_conta h2 {
  margin-bottom: 40px;
  margin: 0 auto 20px auto;
}

.acao_conta h2 span {
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.5em;
  border-bottom: 1px solid #eab883;
  font-weight: 700;
}

.acao_conta input {
  margin: 0 auto 15px auto;
}

@media (max-width: 960px) {
  .acao_conta input {
    width: 90%;
  }
}
</style>



