<template>
  <section>
    <div v-if="loading">
      <PaginaCarregando/>
    </div>
    <transition>
      <div v-if="api" class="titulo_pagina">
        <h1 class="titulo">{{api.Titulo}}</h1>
        <p class="descricao_pagina">{{api.Descricao}}</p>
      <div class="box_3grid">
       <div class="card_3grid" v-for="area in api.Areas" :key="area.Id">
         <div class="img-hover-zoom img-hover-zoom--slowmo">
         <router-link :to="{name: 'paroquias', params: {id: area.Titulo}}">
          <img class="link4" :src="area.Img" :alt="area.Titulo">
         </router-link>
         </div>
            <div class="card_3grid_div">
              <h2 class="card_3grid_link">{{ area.Titulo }}</h2>
              <p class="subtitulo_item">{{ area.NumParoquias }}</p>
            </div>
      </div>
      </div>
      </div>
    </transition>
  </section>
</template>
<script>
import fetchData from '@/mixins/fetchData.js'

export default {
  name: 'areas',
  mixins: [fetchData],
  created () {
    this.fetchData('/areas')
  }
}
</script>

<style scoped>
/* CARD COM 3 FOTOS NA LINHA */

.box_3grid {
  grid-area: box_3grid;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: auto;
  justify-content: space-between;
  gap: 40px;
  margin: 40px 0;
}

.card_3grid {
  flex: 1;
}

.card_3grid_div {
  display: block;
  text-align: center;
  margin-top: 20px;
}

.card_3grid_link {
  display: block;
  margin-top: 10px;
  text-align: center;
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.75em;
  font-weight: 600;
  line-height: 1.1;
}

.card_3grid_link:hover {
  color: #000;
}

@media (max-width: 960px) {
  .box_3grid {
    grid-template-columns: 1fr 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
  .card_3grid img {
    margin: 0 auto;
  }
  .card_3grid_div_link {
    margin-top: 2px;
  }
}

@media (max-width: 760px) {
  .box_3grid {
    grid-template-columns: 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
}
/*****************************/

/* The Image container */
.img-hover-zoom {
  height: 348px; /* Modify this according to your need */
  overflow: hidden; /* Removing this will break the effects */
}

</style>



