<template>
  <section class="areas_pastorais">
    <div v-if="loading">
      <PaginaCarregando/>
    </div>
    <transition>
      <div v-if="api" class="box_6grid">
      <div class="card_6grid" v-for="area in api.Areas" :key="area.Id">
       <router-link :to="{name: 'paroquias', params: {id: area.Titulo}}">
        <div class="img-hover-zoom img-hover-zoom--slowmo">
          <img :src="area.Img" />
        </div>
       </router-link>
      <h2 class="card_6grid_titulo">{{area.Titulo}}</h2>
      <p>{{area.NumParoquias}}</p>
      </div>
      </div>
    </transition>
  </section>
</template>

<script>
import fetchData from '@/mixins/fetchData.js'

export default {
  name: 'areas',
  mixins: [fetchData],
  created () {
    this.fetchData('/areas')
  }
}
</script>

<style scoped>
/* CARD COM 6 FOTOS NA LINHA */

.areas_pastorais {
  grid-area: areas_pastorais;
}

.box_6grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  justify-items: auto;
  justify-content: space-between;
  grid-column-gap: 40px;
  margin: 0 0 40px 0;
}

.card_6grid {
  flex: 1;
  text-align: center;
  min-width: 150px;
}

.card_6grid_titulo {
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.4em;
  font-weight: 700;
  padding: 10px 0 5px 0;
}

.card_6grid p {
  color: #b4ae8a;
  font-size: 0.875em;
  font-weight: 600;
}

@keyframes seta {
  from {
    right: 30px;
  }
  to {
    right: 20px;
  }
}

@media (max-width: 960px) {
  .box_6grid {
    position: relative;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    grid-column-gap: 25px;
    margin: 0 0 20px 0;
  }
  .card_6grid {
    margin-bottom: 16px;
  }

  .card_6grid img {
    min-width: 98px;
  }
  .card_6grid_titulo {
    font-size: 1.2em;
    font-weight: 700;
  }
  .card_6grid p {
    font-size: 0.75em;
  }
  .box_titulo::after {
    content: "";
    position: absolute;
    top: 5px;
    right: 30px;
    color: rgba(0, 0, 0, 0.3);
    animation: seta 0.5s ease-in 0s infinite alternate;
  }
}
/*****************************/

.v-enter,
.v-leave-to {
  opacity: 0;
}

.v-enter {
  transform: translate3d(-40px, 0, 0);
}

.v-leave-to {
  transform: translate3d(40px, 0, 0);
}

.v-enter-active {
  /* .v-leave-active {*/
  transition: all 0.5s;
}

/* The Image container */
.img-hover-zoom {
  height: 169px; /* Modify this according to your need */
  overflow: hidden; /* Removing this will break the effects */
}
</style>



