<template>
  <section>
    <div v-if="loading">
      <PaginaCarregando/>
    </div>
    <transition>
      <div v-if="api" class="titulo_pagina">
        <h1 class="titulo">{{api.TituloCompleto}}</h1>
        <p class="descricao_pagina">{{api.Descricao}}</p>
      <div class="box_4grid">
       <div class="card_4grid" v-for="paroquia in paroquias" :key="paroquia.Email">
        <div class="img-hover-zoom img-hover-zoom--slowmo">
          <img src="../img/estrutura1.jpg" />
        </div>
        <div class="card_4grid_div">
          <h2 class="card_4grid_link">{{paroquia.Titulo}}</h2>
          <span class="subtitulo_item">{{paroquia.Telefone}}</span>
        </div>
      </div>
      </div>
      </div>
    </transition>
  </section>
</template>

<script>
import { db } from '../firebase.js'
import fetchData from '@/mixins/fetchData.js'

export default {
  name: 'paroquias',
  props: ['id'],
  mixins: [fetchData],
  components: {
    db
  },
  data () {
    return {
      paroquias: {}
    }
  },
  created () {
    this.loading2 = true
    this.fetchData(`/areapastoral/${this.id}`)
    db.collection('Paroquias')
      .where('AreaPastoral', '==', this.id)
      .get().then(querySnapshot => {
        this.paroquias = querySnapshot.docs.map(doc => doc.data())
        this.loading2 = false
      })
  }
}
</script>

<style scoped>
/* CARD COM 3 FOTOS NA LINHA */

.box_4grid {
  grid-area: box_4grid;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-items: start;
  justify-content: space-between;
  gap: 40px;
  margin: 40px 0;
}

.card_4grid {
  flex: 1;
  display: block;
  text-align: center;
}

.card_4grid_div {
  display: grid;
  align-content: start;
}

.card_4grid_div img {
  max-width: 251px;
}

.card_4grid_link {
  display: block;
  margin-top: 20px;
  text-align: center;
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.5em;
  font-weight: 600;
  line-height: 1.1;
}

.telefone, .email {
  margin: 10px 0 0 0;
}

.email {
  font-size: 0.8em;;
}

.telefone {
  font-size: 1.3em;
  font-weight: 600;
}

@media (max-width: 960px) {
  .box_4grid {
    grid-template-columns: 1fr 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
  .card_4grid img {
    margin: 0 auto;
  }
  .card_4grid_div_link {
    margin-top: 2px;
  }
}

@media (max-width: 760px) {
  .box_4grid {
    grid-template-columns: 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
}

/* The Image container */
.img-hover-zoom {
  height: 251px; /* Modify this according to your need */
  overflow: hidden; /* Removing this will break the effects */
}
</style>



