<template>
  <section>
    <div v-if="loading" class="titulo_pagina">
      <PaginaCarregando/>
    </div>
    <transition>
      <div v-if="api" class="titulo_pagina">
        <h1 class="titulo">{{api.Titulo}}</h1>
      <div class="box_4grid">
       <div class="card_4grid" v-for="area1 in api.areapastoral" :key="area1.Id">
        <img class="link4" :src="area1.Img" />
        <div class="card_4grid_div">
          <h2 class="card_4grid_link">{{area1.Titulo}}</h2>
        </div>
      </div>
      </div>
      </div>
    </transition>
  </section>
</template>

<script>
import fetchData from '@/mixins/fetchData.js'

export default {
  name: 'areapastoral',
  props: ['id'],
  mixins: [fetchData],
  created () {
    console.log(this.id)
    this.fetchData(`/area1/${this.id}`)
  }
}
</script>

<script>
import fetchData from "@/mixins/fetchData.js";

export default {
  name: "areapastoral",
  props: ["id"],
  mixins: [fetchData],
  created() {
    this.fetchData(`/area1/${this.id}`);
  },
  beforeRouteUpdate(to, from, next) {
    this.fetchData(`/area1/${to.params.id}`);
    next();
  }
};
</script>

<style scoped>
/* CARD COM 3 FOTOS NA LINHA */

.box_3grid {
  grid-area: box_3grid;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: auto;
  justify-content: space-between;
  gap: 40px;
  margin: 40px 0;
}

.card_3grid {
  flex: 1;
}

.card_3grid_div {
  display: block;
  text-align: center;
  margin-top: 20px;
}

.card_3grid_link {
  display: block;
  margin-top: 10px;
  text-align: center;
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.75em;
  font-weight: 600;
  line-height: 1.1;
}

.card_3grid_link:hover {
  color: #000;
}

.num_paroquias {
  color: #B4AE8A;
  font-size: 1.125em;
  font-weight: 600;
  display: block;
  margin-top: 5px;
}

@media (max-width: 960px) {
  .box_3grid {
    grid-template-columns: 1fr 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
  .card_3grid img {
    margin: 0 auto;
  }
  .card_3grid_div_link {
    margin-top: 2px;
  }
}

@media (max-width: 760px) {
  .box_3grid {
    grid-template-columns: 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
}
/*****************************/
</style>



