<style scoped>
/* CARD COM 3 FOTOS NA LINHA */

.box_3grid {
  grid-area: box_3grid;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: auto;
  justify-content: space-between;
  gap: 40px;
  margin: 40px 0;
}

.card_3grid {
  flex: 1;
}

.card_3grid_div {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: start;
  justify-items: start;
  margin-top: 20px;
}

.card_3grid_assets {
  height: 30px;
  display: flex;
}

.card_3grid_assets img {
  max-width: 30px;
}

.card_3grid_assets img + img {
  margin: 0 0 0 10px;
}

.card_3grid_data {
  justify-self: end;
}

.card_3grid_div a {
  flex: 1;
  grid-column: span 3;
  display: block;
  margin-top: 10px;
  text-align: center;
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.75em;
  font-weight: 600;
  line-height: 1.1;
}

.card_3grid_div a:hover {
  color: #000;
}

@media (max-width: 960px) {
  .box_3grid {
    grid-template-columns: 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
  .card_3grid img {
    margin: 0 auto;
  }
  .card_3grid_div a {
    margin-top: 2px;
  }
}
/*****************************/

.v-enter,
.v-leave-to {
  opacity: 0;
}

.v-enter {
  transform: translate3d(-20px, 0, 0);
}

.v-leave-to {
  transform: translate3d(20px, 0, 0);
}

.v-enter-active {
  /* .v-leave-active {*/
  transition: all 0.5s;
}

/* The Image container */
.img-hover-zoom {
  height: 348px; /* Modify this according to your need */
  overflow: hidden; /* Removing this will break the effects */
}
</style>



