<template>
  <nav class="sidenav">
    <ul>
      <li>
        <router-link class="sidenav_link" to="/noticias">Notcias</router-link>
      </li>
      <li>
        <router-link class="sidenav_link" to="/estrutura">Estrutura</router-link>
      </li>
      <li>
        <router-link class="sidenav_link" to="/areaspastorais">Parquias</router-link>
      </li>
      <li>
        <router-link class="sidenav_link" to="/acao_pastoral">Ao Pastoral</router-link>
      </li>
      <li>
        <router-link class="sidenav_link" to="/videos">Videos</router-link>
      </li>
      <li>
        <router-link class="sidenav_link" to="/campanhas">Campanhas</router-link>
      </li>
      <li>
        <router-link class="sidenav_link" to="/servicos">Servios</router-link>
      </li>
      <li>
        <router-link class="sidenav_link" to="/contatos"
          >Fale Conosco</router-link
        >
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: 'Sidenav'
}
</script>

<style scoped>
.sidenav {
  grid-area: sidenav;
  margin-bottom: 30px;
}

.sidenav ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-content: center;
}

.sidenav_link {
  color: #4a5a69;
  font-weight: 500;
  font-size: 1.15em;
  padding: 0 0 3px 0;
}

.sidenav_link:hover {
  color: #000;
  border-bottom: 1px solid #eab883;
}

@media (max-width: 960px) {
  .sidenav {
    display: none;
  }
}

.router-link-exact-active {
  color: #333333;
  border-bottom: 1px solid #eab883;
}
</style>



