<template>
  <header class="header">
    <div class="header_aovivo">
      <img src="../assets/ao_vivo.svg" />
      <p>Rdio Amrica FM 91,1</p>
    </div>

    <router-link to="/" class="logomarca">
      <img src="../img/logomarca_arquidiocese_de_vitoria1.png" />
    </router-link>

    <div class="header_icones">
      <img src="../assets/minha_conta.svg" alt="Minha Conta" />
      <img src="../assets/lupa.svg" alt="Faa uma busca no site" />
    </div>
  </header>
</template>

<script>
export default {
  name: 'Header'
}
</script>

<style scoped>
/* HEADER */

.header {
  grid-area: header;
  grid-template-columns: 1fr 3fr 1fr;
  width: 100%;
  margin: 0 auto;
  display: grid;
  align-items: center;
  flex-wrap: wrap;
  padding: 30px 0 34px 0;
}

.header_aovivo {
  width: 100%;
  display: flex;
  align-items: center;
}

.header_aovivo p {
  margin-left: 10px;
}

.logomarca {
  width: 100%;
  justify-self: center;
  max-width: 240px;
}

.header_icones {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.header_icones img {
  margin: 0 0 0 30px;
}

@media (max-width: 960px) {
  .header {
    padding: 16px 0 16px 0;
  }
  .logomarca {
    max-width: 140px;
  }
  .header_aovivo p {
    display: none;
  }
  .header_icones img {
    margin: 0 0 0 16px;
  }
}
</style>



