import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home.vue'
import Noticias from '../views/Noticias.vue'
import NoticiaView from '../views/NoticiaView.vue'
import Estrutura from '../views/Estrutura.vue'
import EstruturaView from '../views/EstruturaView.vue'
import AreasPastorais from '../views/AreasPastorais.vue'
import AreaPastoral from '../views/AreaPastoral.vue'
import AcaoPastoral from '../views/AcaoPastoral.vue'
import Eventos from '../views/Eventos.vue'
import Campanhas from '../views/Campanhas.vue'
import Servicos from '../views/Servicos.vue'
import Paroquias from '../views/Paroquias.vue'
import Contatos from '../views/Contatos.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/noticias',
      name: 'noticias',
      component: Noticias
    },
    {
      path: '/noticias/:id',
      name: 'noticia',
      component: NoticiaView,
      props: true
    },
    {
      path: '/estrutura',
      name: 'estrutura',
      component: Estrutura
    },
    {
      path: '/estrutura/:id',
      name: 'estrutura',
      component: EstruturaView
    },
    {
      path: '/areaspastorais',
      name: 'areaspastorais',
      component: AreasPastorais
    },
    {
      path: '/areapastoral/:id',
      name: 'areapastoral',
      component: AreaPastoral,
      props: true
    },
    {
      path: '/paroquias/:id',
      name: 'paroquias',
      component: Paroquias,
      props: true
    },
    {
      path: '/acao_pastoral',
      name: 'acao_pastoral',
      component: AcaoPastoral
    },
    {
      path: '/eventos',
      name: 'eventos',
      component: Eventos
    },
    {
      path: '/campanhas',
      name: 'campanhas',
      component: Campanhas
    },
    {
      path: '/servicos',
      name: 'servicos',
      component: Servicos
    },
    {
      path: '/contatos',
      name: 'contatos',
      component: Contatos
    }
  ],
  scrollBehavior () {
    return window.scrollTo({ top: 0, behavior: 'smooth' })
  }
})



