<template>
  <section class="destaque">
    <h1 class="titulo_sub">Destaques</h1>

    <div class="destaque_box">
      <router-link to="/noticias">
        <div class="destaque_item img-hover-zoom--slowmo">
          <img src="../img/destaque1.jpg" />
          <span class="categoria">FESTA DA PENHA</span>
          <a href="#" class="destaque-link"
            >Acompanhe a programao da Festa da Penha 2020</a
          >
        </div>
      </router-link>

      <router-link to="/noticias">
        <div class="destaque_item img-hover-zoom--slowmo">
          <img src="../img/destaque2.jpg" />
          <span class="categoria">AO PASTORAL</span>
          <a href="#" class="destaque-link"
            >Encontro rene lideranas da Catequese da rea Serrana</a
          >
        </div>
      </router-link>

      <router-link to="/noticias">
        <div class="destaque_item img-hover-zoom--slowmo">
          <img src="../img/destaque3.jpg" />
          <span class="categoria">CAMPANHAS</span>
          <a href="#" class="destaque-link"
            >CONDIVIDIR: campanha nica de arrecadao</a
          >
        </div>
      </router-link>

      <router-link to="/noticias">
        <div class="destaque_item img-hover-zoom--slowmo">
          <img src="../img/destaque4.jpg" />
          <span class="categoria">PARQUIAS</span>
          <a href="#" class="destaque-link"
            >Festa de Nossa Senhora dos navegantes em Meape</a
          >
        </div>
      </router-link>
    </div>
  </section>
</template>

<script>
import { db } from '../firebase.js'

export default {
  name: 'Destaque',
  data () {
    return {
      noticias: {}
    }
  },
  firestore: {
    noticias: db.collection('Noticias').orderBy('DataHoraCadastro', 'desc')
  },
  methods: {
    getNoticia (titulo) {
      db.collection('Noticias')
        .doc(titulo)
        .get()
    }
  }
}
</script>

<style scoped>
/* DESTAQUE */

.destaque {
  grid-area: destaque;
}

.destaque_box {
  grid-area: content;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 40px;
  padding: 0 0 0 40px;
}

.destaque_item {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: 81px 1fr;
  grid-column-gap: 15px;
  justify-items: start;
  align-items: start;
  height: 81px; /* Modify this according to your need */
  overflow: hidden;
}

.destaque_item img {
  grid-row: span 2;
}

.destaque_item a {
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.3em;
  font-weight: 700;
  margin-top: 6px;
  text-align: left;
}

.destaque_item a:hover {
  color: #000;
  text-decoration: inherit;
}

@media (max-width: 960px) {
  .destaque {
    margin-top: 32px;
  }
  .destaque_box {
    padding: 0;
  }
  .destaque h1 {
    display: none;
  }
  .destaque_item a {
    font-size: 1.5em;
    font-weight: 700;
  }
}

/*********************************/

/* The Image container */
.img-hover-zoom {
  height: 81px; /* Modify this according to your need */
  overflow: hidden; /* Removing this will break the effects */
}
</style>



