<template>
  <div class="titulo_pagina">
    <section class="noticia_item">
      <img class="noticia_item_foto" src="../img/foto_noticia_item.jpg" />

      <div class="noticia_item_div">
        <span class="categoria">ESTRUTURA</span>
        <span class="data">11/04/2020 15h35</span>
        <img class="icone" src="../assets/compartilhar_rosa.svg" />
        <h1 class="titulo_item">
          {{ noticia.Titulo }}
        </h1>
      </div>

      <div class="noticia_div">
        <div class="destaque_lateral">
          <Destaque />
        </div>
        <div class="conteudo">
          {{ noticia.Conteudo }}
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { db } from '../firebase.js'
import Destaque from '../components/Destaque.vue'

export default {
  name: 'NoticiaView',
  props: ['id'],
  components: {
    Destaque,
    db
  },
  data () {
    return {
      noticia: {}
    }
  },
  created () {
    const ref = db.collection('Noticias').doc(this.id)
    ref.get().then(doc => {
      if (doc.exists) {
        this.noticia = doc.data()
      } else {
        alert('Documento no encontrado')
      }
    })
  }
}
</script>

<style scoped>
.noticia_item {
  margin: 40px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}

.noticia_item_div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: baseline;
  align-content: center;
  margin-top: 20px;
}

.categoria {
  grid-column: span 2;
  justify-self: center;
}

.data {
  justify-self: start;
}

.icone {
  justify-self: end;
}

.noticia_div {
  grid-column: span 2;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 40px;
}
</style>



