<template>
  <transition mode="out-in">
    <section class="box_3grid">
      <div class="card_3grid">
        <a href="#">
          <img src="../img/estrutura1.jpg" />
        </a>
        <div class="card_3grid_div">
          <h2 class="card_3grid_link">Histria da Arquidiocese</h2>
        </div>
      </div>

      <div class="card_3grid">
        <a href="#">
          <img src="../img/estrutura2.jpg" />
        </a>
        <div class="card_3grid_div">
          <h2 class="card_3grid_link">Arcebispo / Bispos</h2>
        </div>
      </div>

      <div class="card_3grid">
        <a href="#">
          <img src="../img/estrutura3.jpg" />
        </a>
        <div class="card_3grid_div">
          <h2 class="card_3grid_link">Vigrios Gerais</h2>
        </div>
      </div>
    </section>
  </transition>
</template>

<script>
export default {
  name: 'ServicosGrid3'
}
</script>

<style scoped>
/* CARD COM 3 FOTOS NA LINHA */

.box_3grid {
  grid-area: box_3grid;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: auto;
  justify-content: space-between;
  gap: 40px;
  margin: 40px 0;
}

.card_3grid {
  flex: 1;
}

.card_3grid_div {
  display: block;
  text-align: center;
  margin-top: 20px;
}

.card_3grid_link {
  display: block;
  margin-top: 10px;
  text-align: center;
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.75em;
  font-weight: 600;
  line-height: 1.1;
}

.card_3grid_link:hover {
  color: #000;
}

@media (max-width: 960px) {
  .box_3grid {
    grid-template-columns: 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
  .card_3grid img {
    margin: 0 auto;
  }
  .card_3grid_div_link {
    margin-top: 2px;
  }
}
/*****************************/

.v-enter,
.v-leave-to {
  opacity: 0;
}

.v-enter {
  transform: translate3d(-20px, 0, 0);
}

.v-leave-to {
  transform: translate3d(20px, 0, 0);
}

.v-enter-active {
  /* .v-leave-active {*/
  transition: all 0.5s;
}
</style>



