<template>
  <div class="slider_noticias">

    <div class="img-hover-zoom img-hover-zoom--slowmo">
      <a href="#">
        <img src="../img/noticia1.jpg" alt="Another Image zoom-on-hover effect">
      </a>
    </div>

    <div class="slider_noticias_div">
      <span class="card_3grid_assets"></span>
      <span class="categoria">FESTA DA PENHA</span>
      <span class="data slider_noticias_data">11/04/2020</span>
    </div>

    <h1>
      <a href="#"
        >Comeou o Oitavrio da Penha com a presena de Dom Dario Campos</a
      >
    </h1>
  </div>
</template>

<script>
export default {
  name: 'SliderNoticias'
}
</script>

<style scoped>
/* SLIDER NOTCIAS */
.slider_noticias {
  grid-area: slider_noticias;
  text-align: center;
  position: relative;
}

.slider_noticias_foto {
  margin-bottom: 20px;
}

.slider_noticias_div {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  margin-top: 20px;
}

.slider_noticias_assets {
  height: 30px;
  display: flex;
}

.slider_noticias_assets img {
  max-width: 30px;
}

.slider_noticias_assets img + img {
  margin: 0 0 0 10px;
}

.slider_noticias_data {
  justify-self: end;
}

.slider_noticias h1 {
  margin-top: 5px;
  padding: 0 20px;
}

.slider_noticias h1 a {
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 2.4rem;
  font-weight: 700;
}

.slider_noticias h1 a:hover {
  color: #000;
  text-decoration: inherit;
}

@media (max-width: 960px) {
  .slider_noticias h1 {
    line-height: 1;
    padding: 0;
  }
  .slider_noticias h1 a {
    font-size: 2rem;
  }
}

/*****************************/

/* The Image container */
.img-hover-zoom {
  height: 431px; /* Modify this according to your need */
  overflow: hidden; /* Removing this will break the effects */
}

</style>



